import React, { useState, useCallback } from 'react';
import JSZip from 'jszip';
import html2canvas from 'html2canvas'; 
import Item from './Item';
import './App.css';

function App() {

  const [baseUrl, setBaseUrl] = useState('http://ai.esmplus.com/jin465/summer_set'); // 기본 주소
  const [items, setItems] = useState([]);
  const [, updateState] = React.useState();
  const forceUpdate = useCallback(() => updateState({}), []);

  const states = () => {
    return {
      baseUrl: baseUrl,
      items: items,
      setItems: setItems,
      forceUpdate: forceUpdate
    }
  }

  const downloadFile = (filename, file) => {
    const element = document.createElement("a");
    const f = new Blob([file], {type: 'text/plain'});
    element.href = URL.createObjectURL(f);
    element.download = filename;
    document.body.appendChild(element); // Required for this to work in FireFox
    element.click();
  }

  const renderItems = [];

  for(let i = 0;i<items.length;i+=2) {
    if(i+1 == items.length) {
      const item1 = items[i];
      renderItems.push([item1])
      break;
    }
    const item1 = items[i];
    const item2 = items[i+1];
    renderItems.push([item1, item2])
  }

  const addItem = (item) => {
    setItems([...items, item]);
  }

  const onClickBtn = () => {
    const item = {
      name: ''
    , price: 0
    , colors: ''
    , sizes: '110,120,130,140,150'
    };
    addItem(item);
  }

  const onBaseUrlChanged = (e) => {
    setBaseUrl(e.target.value);
}

  const onClickBtn2 = async () => {
    window.scrollTo(0,0);
    const zip = new JSZip();
    const frames = document.getElementsByClassName('frame')
    for (let i=0;i<frames.length;i++) {
      const e = frames[i];
      const canvas = await html2canvas(e
      , {
        allowTaint: true
        , useCORS: true
        , letterRendering: 1
        , width: 360
        , height: 660
        , scale: 2.167
        , scrollX: 0
        , scrollY: 0
      })

      const img = canvas.toDataURL('image/png').split('base64,')[1];
      zip.folder('card').file(`item${i+1}.png`, img, {base64: true});
    }

    const head = 
    `<!DOCTYPE html>
    <html lang="ko">
    <head>
        <meta charset="UTF-8">
        <meta name="viewport" content="width=device-width, initial-scale=1.0">
        <style>
        * {
          margin:0;
          padding:0;
          font-size: 0;
      }
      .frame {
          position: relative;
          width: 48vw;
          height: 85vw;
      }
      .frame:hover > :not(.show_detail) {
          opacity: 0.5;
      }
      .frame:hover > .show_detail {
          visibility: visible;
      }
      .show_detail {
          position: absolute;
          top: 12vw;
          width: 48vw;
          height: 48vw;
          font-size: 4vw;
          text-align: center;
          font-weight: bold;
          padding-top: 4vw;
          border-radius: 2.6vw;
          color: rgb(51, 51, 51);
          visibility: hidden;
          z-index: 10;
          cursor: default;
          -ms-user-select: none; 
          -moz-user-select: -moz-none; 
          -webkit-user-select: none; 
          -khtml-user-select: none; 
          user-select:none;
      }
      .card {
          object-fit: cover;
          width: 47.2vw;
      }
        </style>
        <body>
            <center>
                <img src="http://ai.esmplus.com/jin465/information/information.png" width="100%" border="0"></a>
                <br>
                <img src="http://ai.esmplus.com/jin465/information2/detail.jpg" width="100%" border="0"></a>
                <br><br>
                <table>
                    <tbody>
                    `;

                    for(const idx in items) {
                      const item = items[idx];
                      
                      console.log(`${idx}, ${item.name}, ${item.price}, ${item.colors}, ${item.sizes}`);
                    }
        let body = ''
        for(let i = 0;i<renderItems.length;i++) {
          const r = renderItems[i];
          body += '<tr>'
          for(let j = 0;j<r.length;j++) {
            const iid = 2*i+j+1;
            body += '<td>'
            const e = r[j];
            body +=
            `<div class="frame" onclick="window.open(this.getAttribute('href'), '', 'width=800, height=10000, scrollbars, resizable, toolbar=no')"  
            href="${baseUrl}/html/item${iid}.html"> 
                <div class="show_detail">클릭 시<br/>상품 상세정보가<br/>나타납니다.</div>
                <img class="card" 
                src='${baseUrl}/card/item${iid}.png' />
            </div>`;
            body += '</td>'

            const htmlFile = 
            `<!DOCTYPE html>
            <html lang="ko">
            <head>
                <meta charset="UTF-8">
                <meta name="viewport" content="width=device-width, initial-scale=1.0">
                <title>${e.name}</title>
            </head>
            <body>
                <style>
                    body {
                        margin: 0;
                        padding: 0;
                        text-align: center;
                    }
                </style>
                <img src='${baseUrl}/item${iid}/detail.jpg' width='100%' alt='${e.name}'>
            </body>
            </html>`;
            zip.folder('html').file(`item${iid}.html`, htmlFile);
            // downloadFile(`item${iid}.html`, htmlFile);
          }
          if(r.length == 1) {
            body += '<td></td>'
          }
          body += '</tr>'
        }
        const foot = 
            `</tbody>
          </table>
        </center>
      </body>
    </html>`

    const html = head+body+foot;
    zip.file(`main.html`, html);
    // downloadFile(`main.html`, html);
    zip.generateAsync({type: 'blob'})
    .then((blob) => {
      downloadFile('Orange.zip', blob);
    })
    // console.log(html);
  }

  // <img src='http://ai.esmplus.com/jin465/information/information.png'/>
  // <img src='http://ai.esmplus.com/jin465/information2/detail.jpg'/>
  let id = 0;
  return (
    <div>
      <div className="topBar">
    <p className="txt">기본 주소 : </p>
    <input className='baseUrl' value={baseUrl} onChange={onBaseUrlChanged}></input>
    <button className="add" onClick={onClickBtn}>추가</button>
      <button className="download" onClick={onClickBtn2}>다운로드</button>
      </div>
      <center>
      <table>
          {
          renderItems.map(
            (r) => {
              return <tr>
              {r.map(e => <td><Item id={++id} item={e} states={states}/></td>
              )}
              </tr>
            }
          )
        }
      </table>
      </center>
    </div>
  );
}

export default App;
